.nav-box-card {
 position: relative;
 background-size: cover;
 background-position: center;
 overflow: hidden;

 transition: all 0.3s ease;
 transform: translateY(0);
}

.nav-box-card:hover {
 transform: translateY(-5px) scale(1.02);
 box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.2);
}

.nav-box-card::before {
 content: '';
 position: absolute;
 top: 0;
 left: 0;
 width: 100%;
 height: 100%;
 background: rgba(0, 0, 0, 0.5);
 z-index: 1;
 transition: all 0.3s ease;
}

.nav-box-card p,
.nav-box-card h3 {
 position: relative;
 z-index: 2;
}

.nav-box-card:hover::before {
 background: rgba(0, 0, 0, 0.7);
}